'use client';

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import Image from "next/image";
import { Plus, Trash2 } from "lucide-react";

export default function FileUpload({ productId }: { productId: number }) {
  const [images, setImages] = useState<string[]>([]);

  const handleImageUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files) {
      try {
        // Log the success for each file
        Array.from(files).forEach((file) => {
          console.log("File uploaded:", file.name);
        });

        // Instead of uploading, just log for now
        const uploadedImages = Array.from(files).map((file) => URL.createObjectURL(file));
        setImages((prev) => [...prev, ...uploadedImages]);

        console.log("Images successfully uploaded (logged):", uploadedImages);

      } catch (error) {
        console.error("Error uploading images:", error);
      }
    }
  };

  const saveImages = async () => {
    try {
      // Just log the current images in state
      console.log("Images saved (logged):", images);
    } catch (error) {
      console.error("Failed to save images:", error);
    }
  };

  return (
    <div className="space-y-8">
      {/* Product Images */}
      <div className="space-y-4">
        <Label className="text-lg font-semibold">
          Product Images<span className="text-red-500">*</span>
        </Label>
        <div className="grid grid-cols-4 gap-4">
          {images.map((image, index) => (
            <div key={index} className="relative aspect-[3/4] bg-gray-100 rounded-lg overflow-hidden">
              <Image
                src={image}
                alt={`Product ${index + 1}`}
                fill
                className="object-cover"
              />
              <Button
                variant="destructive"
                size="icon"
                className="absolute top-2 right-2 h-8 w-8"
                onClick={() => setImages((prev) => prev.filter((_, i) => i !== index))}
              >
                <Trash2 className="h-4 w-4" />
              </Button>
            </div>
          ))}
          <label className="aspect-[3/4] border-2 border-dashed rounded-lg flex flex-col items-center justify-center cursor-pointer hover:bg-gray-50">
            <Plus className="h-8 w-8 text-gray-400" />
            <span className="text-sm text-gray-500 mt-2">Add more</span>
            <Input
              type="file"
              className="hidden"
              accept="image/*"
              multiple
              onChange={handleImageUpload}
            />
          </label>
        </div>
        <Button onClick={saveImages} disabled={images.length === 0}>
          Save Images (Logged)
        </Button>
      </div>
    </div>
  );
}
